import { createSlice } from "@reduxjs/toolkit";
import storage from "redux-persist/lib/storage";
import { persistReducer } from "redux-persist";

const initialState = {
  contacts: [
    { id: "id-1", name: "Rosie Simpson", number: "459-12-56" },
    { id: "id-2", name: "Hermione Kline", number: "443-89-12" },
    { id: "id-3", name: "Eden Clements", number: "645-17-79" },
    { id: "id-4", name: "Annie Copeland", number: "227-91-26" },
  ],
  filter: "",
};

export const phonebookSlice = createSlice({
  name: "phonebook",
  initialState,
  reducers: {
    addContactToPhonebook: (state, action) => {
      state.contacts.push(action.payload);
    },

    removeContactFromPhonebook: (state, action) => {
      // return state.contacts.filter(contact => contact.id !== action.payload); -   ,     ,    state.contacts
      // return     contacts,  ,     . Redux Toolkit     ,    '/   .
      //   initialState  ,       return:
      // const initialState = [
      //     { id: "id-1", name: "Rosie Simpson", number: "459-12-56" },
      //     { id: "id-2", name: "Hermione Kline", number: "443-89-12" },
      //     { id: "id-3", name: "Eden Clements", number: "645-17-79" },
      //     { id: "id-4", name: "Annie Copeland", number: "227-91-26" },
      // ],

      state.contacts = state.contacts.filter(
        contact => contact.id !== action.payload,
      );
    },

    changeFilter: (state, action) => {
      state.filter = action.payload;
    },
  },
});

export const {
  addContactToPhonebook,
  removeContactFromPhonebook,
  changeFilter,
} = phonebookSlice.actions;

const persistConfig = {
  key: "phonebook",
  storage,
};

export const persistedPhonebookReducer = persistReducer(
  persistConfig,
  phonebookSlice.reducer,
);
